a:hover {
    font-size: 20px;
}

nav #menu li .span {
    font-weight: 700;
    color: white;
}

nav #menu li a {
    color: white;
}

nav #menu li a:hover {
    text-decoration: none;
}

#toggle {
    position: absolute;
    right: 30px;
    top: 20px;
    font-weight: 300;
    z-index: 2;
    width: 30px;
    height: 30px;
    cursor: pointer;
    float: right;
    -moz-transition: all 0.3s ease-out;
    visibility: hidden;
    opacity: 0;
    color: white;
}

.close-btn {
    position: absolute;
    right: 16px;
    font-weight: 300;
    z-index: 2;
    cursor: pointer;
    top: -2px;
    line-height: 80px;
}

#resize {
    z-index: 1;
    top: 10px;
    position: fixed;
    background: #0f0f0f;
    width: 100%;
    height: 100%;
    display: none;
    opacity: 0;
    -moz-transition: all 1s ease-out;
}

#resize #menu {
    height: 90px;
    position: absolute;
    left: 45%;
    transform: translateX(-40);
    text-align: center;
    display: table-cell;
    vertical-align: center;
}

#resize #menu li{
    display: block;
    text-align: center;
    padding: 10px 0;
    font-size: 50px;
    min-height: 50px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease-out;
}

#resize li:nth-child(1){
    margin-top: 140px;
}

#resize #menu li a{
    color: white;
}

#resize #menu li a:hover {
    text-decoration: none;
}

#resize :active {
    visibility: visible;
    opacity: 1;
}

@media(max-width: 768px){
    #toggle {
        visibility: visible;
        opacity: 1;
        margin-top: 6px;
        margin-right: 4px;
    }
    #toggle .span{
        margin-left: 30px;
        color: white;
        position: relative;
        right: -300px;
        font-size: x-large;
    } 
    nav #brand {
        margin-left: 24px;
    }
    #menu a {
        font-size: 20px;
        font-weight: 300;
    }
    #resize li .span {
        font-weight: bolder;
    }
    nav #menu {
        display: none;
    }
}
@media(min-width: 768px){
    #resize{
        visibility: hidden !important;
    }
}
